import React from "react";
import { BrowserRouter as Router, Route, Switch} from "react-router-dom";
import './App.css';
import Home from "./Home";
import Header from "./Header";

function App() {
  return (
    <Router>
    <div className="App">
       <Switch>
         <Route path = "/">
           <Header />
           <Home />
           </Route>
         </Switch>
       </div>
    </Router>
  );
}

export default App;
